@import './modern-normolize.css';
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}
.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgb(61, 55, 55);
  border-radius: 4px;
  width: 368px;
  margin: 20px auto;
}

.friends {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
  width: 300px;
  margin: 20px auto;
}

.transactions {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
  margin: 20px auto;
}
